* {
  box-sizing: border-box;
}

.hidden {
  display: none;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 2px solid #ff3d00;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: relative;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 12px;
  height: 12px;
  border: 2px solid #fff;
  border-radius: 50%;
  position: absolute;
  top: 4px;
  left: 4px;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

